@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');


$bg2: #1b1b1b;
$bg1: #363636;
$bg0: #464646;

$tc0: whitesmoke;
$tc1: #efb44e;

$groupSpacing: 20px;

$rounded: 0px;

.dynamic-forms {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}

.dynamic-forms::-webkit-scrollbar {
    display: none;
}

.dynamic-forms.dark {
    background: $bg0;

    form {

        padding: 20px;

        >ul {
            list-style: none;
            padding: 0px;
            margin: 0px;
            color: whitesmoke;

            input {
                width: 100%;
                padding: 9px;
                background: transparent;
                box-sizing: border-box;
                border: 0px;
                color: $tc0;
                font-size: 14px;

                &:focus {
                    outline: none;
                }
            }

            >li {
                background: $bg1;
                border: 1px solid $bg2;
                border-radius: $rounded;
                margin-bottom: $groupSpacing;
                
                // &:hover {
                //     -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
                //     -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
                //     box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
                // }



                >label {
                    display: block;
                    position: absolute;
                    margin-top: -8px;
                    margin-left: 5px;
                    background: $bg1;
                    padding: 5px;
                    padding-top: 3px;
                    padding-bottom: 0px;
                    font-size: 10px;
                    border-radius: $rounded - 2px;

                    &:empty{
                        display: none
                    }
                }

                >input {
                    padding-bottom: 5px;
                }

                >span {
                    background: $bg2;
                    box-sizing: border-box;;
                    display: block;
                    width: 100%;
                    padding: 3px;
                    text-align: center;
                    color: #C0C0C0;
                    font-family: Arial, Helvetica, sans-serif;
                    font-size: 11px;
                    clear: both;
                }

                >span:empty {
                    height: 1px;
                    padding: 0px;
                    margin: 0px;
                }

                // =================================
                // TEXTBOX
                // ================================

                &.fb-element-text > label {

                }
                &.fb-element-text > input {
                    
                }

                &.fb-element-text > span {
                }

                // =================================
                // CHECKBOX
                // ================================
                

                &.fb-element-checkbox > label {

                    position: unset;
                    display: unset;
                    margin: 0px;
                    margin-left: 2px;
                    margin-top: 5px;
                    font-size: 12px;
                    float: left;
                }

                &.fb-element-checkbox > input {
                    width: 16px;
                    height: 24px;
                    float: left;
                    margin-left: 8px;
                    
                }

                &.fb-element-checkbox > span:before {
                    content: '';
                }

                // =================================
                // SELECT
                // ================================
                
                &.fb-element-select > select {
                    border: 0px;
                    padding: 9px;
                    padding-bottom: 5px;
                    background: transparent;
                    outline: none;
                    color: $tc0;
                    width: 100%;
                    box-sizing: border-box;

                    > option {
                        background: $bg0;
                    }
                }


                // =================================
                // ARRAY - INPUTS
                // ================================

                &.fb-element-array {
                    border-right: 0px solid $bg2;
                }

                &.fb-element-array > ul {
                    list-style: none;
                    padding: 0px;
                    margin: 0px;
                    display: flex;
                    flex-wrap: wrap;

                    >li {
                        border-bottom: 1px solid $bg2;
                        border-right: 1px solid $bg2;
                        flex: 1;

                        &.break {
                            flex-basis: 100%;
                            height: 0;
                            border: none;
                        }
                    }
                }

                // =================================
                // OBJECT - INPUTS
                // ================================

                &.fb-element-object {
                    border-right: 0px solid $bg2;
                }

                &.fb-element-object > ul {
                    list-style: none;
                    padding: 0px;
                    margin: 0px;
                    color: $tc0;
                    display: flex;
                    flex-wrap: wrap;
                    

                    >li {
                        border-bottom: 1px solid $bg2;
                        border-right: 1px solid $bg2;
                        flex: 1;

                        &.break {
                            flex-basis: 100%;
                            height: 0;
                            border: none;
                        }

                        input {
                            padding-top: 2px;
                        }

                        label {
                            color: $tc1;
                            font-size: x-small;
                            margin-left: 10px;
                        }

                        
                    }

                    

                    button {
                        width: 100%;
                        height: 100%;
                        background: transparent;
                        border: none;
                        padding: 9px;
                        color: $tc0;
                        text-align: left;
                        cursor: pointer;
                    }

                    button:hover {
                        background: $bg2;
                        color: $tc1;
                    }
                }

                // =================================
                // MARKDOWN
                // ================================

                &.fb-element-markdown {
                    border: 0px;
                    background: transparent;

                    &:hover {
                        box-shadow: none;
                    }

                    pre {
                        background: $bg1;
                        color: $tc0;
                        padding: 10px;
                    }

                    hr {
                        color: $tc0;
                        background: $tc0;
                    }
                }

                // =================================
                // BUTTON
                // ================================

                &.fb-element-button {

                    background: none;
                    border: none;
                    &:hover { box-shadow: none; }

                    button {
                        width: 100%;
                        background: $bg2;
                        outline: none;
                        border: 1px solid $bg2;
                        border-radius: 5px;;
                        padding: 10px;
                        color: $tc0;

                        &:hover {
                            color: $tc1;
                            cursor: pointer;
                            -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
                            -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
                            box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
                        }
                    }

                }
                                
            }

            

            

        }

    }
}